import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Activity, Plus } from "lucide-react";
import { useForm } from "react-hook-form";



export default function DashboardForm() {

    const { register, handleSubmit } = useForm()

    function useHandleSubmit (data: any) {
        console.log(data)
    }

    return(
        <div className="container mx-auto p-4 space-y-8">
            <Card>
                <CardHeader>
                    <CardTitle className="text-2xl font-bold flex items-center gap-2">
                        <Activity className="text-red-600" />
                           Dashboard de corridas
                    </CardTitle>
                </CardHeader>

                <CardContent>
                    <form onSubmit={handleSubmit(useHandleSubmit)}>
                        <div className="space-y-4">
                            <Label>Data e Hora</Label>
                            <Input {...register("date")}/>                         
                        </div>       

                        <div className="space-y-4 mt-4">
                            <Label>Distância (Km)</Label>
                            <Input {...register("distance")}/>                         
                        </div>    

                        <div className="space-y-4 mt-4">
                            <Label>Duração (min)</Label>
                            <Input {...register("duration")}/>                         
                        </div>        


                        <div className="space-y-2 mt-4">
                            <Label>Pace (min/Km)</Label>
                            <Input {...register("pace")}/>                         
                        </div>     

                        <div className="space-y-2 mt-4">
                            <Label>Calorias queimadas</Label>
                            <Input {...register("calory")}/>                         
                        </div>   

                        <div className="space-x-4 mt-6">
                          <Button>
                             <Plus className="text-red-600"/> Adicionar Corrida
                            </Button>    
                        </div>    
                    </form>



                </CardContent>
            </Card>

        </div>
    )
}